module.exports = {"ranks":[{"text":"As the story progresses, you'll unlock the ability to befriend Sojiro.","rank":"1"},{"text":"Impactful Conversation Choices for this rank:Unlocks Coffee Basics, a new ability that lets you create a coffee item that can restore SP in battle using LeBlanc's coffee brewing facilities. Kindness +3.","rank":"2","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"Making coffee.\"","points":"2"}]},{"choice":"Choice 2:","options":[{"dialogue":"\"That guy seemed suspicious.\"","points":"2"}]},{"choice":"Choice 3:","options":[{"dialogue":"\"I want the ladies to love me.\"","points":"2"}]}]},{"text":"Before you start this rank, you'll have to make coffee with Sojiro once. Afterwards, you'll be able to talk further. Impactful conversation choices for this rank:","rank":"3","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"Medium-fine.\"","points":"2"}]},{"choice":"Choice 3:","options":[{"dialogue":"\"Is it trouble?\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:Unlocks the Leblanc Curry ability - this skill lets ytou create a curry item that can restore SP using LeBlanc's kitchen. Kindness +3","rank":"4","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"Tell me more.\"","points":"3"}]},{"choice":"Choice 2:","options":[{"dialogue":"\"Call Sojiro's phone.\"","points":"3"}]}]},{"text":"Available only after 8/22. Impactful conversation choices for this rank:Kindness +3","rank":"5","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"I think I'm addicted!\"","points":"3"},{"dialogue":"\"It wasn't bad.\"","points":"2"}]},{"choice":"Choice 2:","options":[{"dialogue":"\"She wasn't normal, huh?\"","points":"3"},{"dialogue":"\"She was like Futaba?\"","points":"2"}]},{"choice":"Choice 3:","options":[{"dialogue":"\"It really paid off in the end.\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:Unlocks the Coffee Mastery ability, enhancing your coffee-making ability to allow for better items. Kindness +3","rank":"6","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"To each his own.\"","points":"2"}]},{"choice":"Choice 2:","options":[{"dialogue":"\"Shut your mouth.\"","points":"3"},{"dialogue":"\"You're wrong.\"","points":"2"}]},{"choice":"Choice 3:","options":[{"dialogue":"\"Saving Futaba was no mistake.\"","points":"2"}]}]},{"text":"Requires rank 5 (angelic) kindness. Impactful conversation choices for this rank:Kindness +3","rank":"7","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"You might be right.\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:Unlocks the Mementos Mission Request: \"The Money-Grubbing Uncle. Futaba and Sojiro Cooperations locked until this is complete.","rank":"8","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"Something with curry.\"","points":"3"},{"dialogue":"\"You want my suggestion?\"","points":"2"}]},{"choice":"Choice 2:","options":[{"dialogue":"\"I was just protecting Futaba.\"","points":"2"}]}]},{"text":"You must complete \"The Money-Grubbing Uncle\" sidequest in order to start this rank. Impactful conversation choices for this rank:Unlocks the Curry Tips skill, which enhances the quality of the curry you can make.","rank":"9","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"It's great.\"","points":"3"},{"dialogue":"\"It's a bit of a nag.\"","points":"2"}]},{"choice":"Choice 2:","options":[{"dialogue":"\"You have a great daughter.\"","points":"3"},{"dialogue":"\"Congrats.\"","points":"3"}]}]},{"text":"Any choices you make will be irrelevant, since this is the max rank. Unlocks the Kanda Church location. Unlocks Curry Mastery, making the curry you make fantastic at restoring your whole party's SP.","rank":"10"}],"arcana":"Hierophant","name":"Sojiro","location":"LeBlanc Cafe at night"}

