module.exports = {"ranks":[{"text":"This cooperation can be picked up from 5/6 onwards, but is entirely optional and easy to miss for months. Yoshida can be found in Station Square, but if you listen to his political preaching you'll learn that he frequents a nearby Beef Bowl Shop. You can apply to work there via the leaflets in the train station. Work there twice and you'll meet Yoshida, starting off this confidant cooperation.","rank":"1"},{"text":"Impactful Conversation Choices for this Rank:Unlocks Diplomacy, a useful ability that lets you get more money or items from shadow negotiation.","rank":"2","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"I want to improve my speech.\"","points":"2"}]},{"choice":"Choice 2:","options":[{"dialogue":"\"One with conviction.\"","points":"2"},{"dialogue":"\"I don't know yet.\"","points":"2"}]},{"choice":"Choice 3:","options":[{"dialogue":"\"That was helpful.\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:Unlocks Fundraising, which allows you to demand a lot more money during shadow negotiation.","rank":"3","choices":[{"choice":"Choice 2:","options":[{"dialogue":"\"His message.\"","points":"2"},{"dialogue":"\"His speaking skills.\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:","rank":"4","choices":[{"choice":"Choice 4:","options":[{"dialogue":"\"I couldn't help myself.\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:Unlocks Manipulation, a skill that raises the chance enemies will give up rare items.","rank":"5","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"That media doesn't matter.\"","points":"3"},{"dialogue":"\"I think so.\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:","rank":"6","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"I don't really care.\"","points":"3"},{"dialogue":"\"I'm for them.\"","points":"2"}]},{"choice":"Choice 2:","options":[{"dialogue":"\"I'd decline.\"","points":"3"},{"dialogue":"\"I'd leave it to chance.\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:","rank":"7","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"You should decline.\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:Unlocks Mind Control, an ability that sometimes lets you skip shadow negotiation entirely.","rank":"8","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"That's a difficult decision.\"","points":"2"}]},{"choice":"Choice 3:","options":[{"dialogue":"\"Stick to your beliefs.\"","points":"2"}]}]},{"text":"Impactful conversation choices for this rank:","rank":"9","choices":[{"choice":"Choice 1:","options":[{"dialogue":"\"Do your best.\"","points":"3"},{"dialogue":"\"I'm getting nervous.\"","points":"2"}]},{"choice":"Choice 2:","options":[{"dialogue":"\"You had a change of heart.\"","points":"2"}]}]},{"text":"Any choices you make will be irrelevant, since this is the max rank. Unlocks Charismatic Speech, allowing for contracts to be forged with higher-level Persona more easily.","rank":"10"}],"arcana":"Sun","name":"Yoshida","location":"Shibuya Station Square at night"}

